import { registerBlockType } from '@wordpress/blocks';
import { edit } from './edit';
import save from './save';
import './style.css';
// const allowedBlocks = ["gymnasium-wellingdorf/promotion-carousel-page"];
const registerPromotionCarousel = () => registerBlockType('gymnasium-wellingdorf/promotion-carousel', {
    title: 'Promotion Carousel',
    category: 'design',
    attributes: {
        promotions: {
            type: 'array',
            default: []
        },
        scrollDuration: {
            type: 'number'
        },
        autoScrollDuration: {
            type: 'number'
        }
    },
    edit,
    save
});
export default registerPromotionCarousel;
